* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #c0c0c0;
  color: #212121;
  line-height: 1.5;

  display: flex;
  flex-direction: column;
  align-items: center;
  height: auto;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
h1 {
  text-transform: uppercase;
}
label {
  display: flex;
  flex-direction: column;
}
.country-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.country-list__element {
  max-width: 500px;
  max-height: 100px;
  overflow: hidden;
  margin: 0 auto;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.country-list__element p {
  margin-block: 10px 0;
}
.country-list__element img {
  width: 90px;
  height: auto;
}
.country-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 40ch;
  text-align: center;
}
.country-info img {
  width: 200px;
  height: auto;
}
.bold {
  font-weight: 700;
}
